#cardFrame {
  flex: 1;
  padding: 20px;
  transition: all .25s ease-in-out;
  border: .3px black solid;
  margin-bottom: 3%;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

#cardFrame:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 22px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  z-index: 2;
  cursor: pointer;
}

img {
  margin-left: auto;
}

#cardHeader {
  flex: 1;
  justify-content: flex-start;
}

#cardMiddle {
  flex: 1;
  margin: 20px 10px 10px 20px;
  font-size: 18px;
}

#cardText {
  flex: 1;
}

.tag {
  font-size: .75rem;
  margin-right: 6px;
  margin-bottom: 6px;
  background-color: #175B81;
  color: white;
  border: none;
}

.tag:hover {
  background-color: #175B81;
}

.category {
  font-size: .75rem;
  margin-right: 6px;
  margin-bottom: 6px;
  background-color: red;
  border: none;
}

.category:hover {
  background-color: red;
}