#search {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-left: 8%;
  margin-right: 8%;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  scrollbar-color: white;
}

#search::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background-color: white; /* make scrollbar transparent */
}

.buttonScroll {
  color: white;
  display: flex;
  margin-right: 10px;
  border-radius: 5px;
  background-color: #3a77b4;
  border: none;
  padding: 8px 18px 8px 18px;
  font-size: 16px;
  transition: all .2s ease-in-out;
}

.buttonScroll:hover {
  transform: scale(1.025);
  background-color: #175B81;
  color: white;
}
